<template>
  <div id="app">
    <div class="header">
      <el-col :span="24"><div>用户管理</div></el-col>
    </div>

    <div class="handle">
      <div class="search">
        <el-button size="mini" @click="dialogFormVisible = true"
          >新建</el-button
        >
        <el-input
          v-model="searchKey"
          class="search-input"
          placeholder="按名字搜索"
        ></el-input>
        <el-button size="mini" @click="search">
          <i class="el-icon-search"></i>
        </el-button>
        <el-button style="margin-left: 0" size="mini" @click="returnData"
          >重置</el-button
        >
      </div>
      <div class="repeal">
        <el-button size="mini" @click="repeal">撤销</el-button>
      </div>
    </div>

    <Table :tableData="tableData" @openDia="openDia"></Table>

    <el-dialog
      title="新建/编辑用户"
      width="550px"
      @close="cancel"
      :visible.sync="dialogFormVisible"
    >
      <el-form
        :rules="rules"
        :inline="true"
        ref="submitForm"
        :model="formInline"
        label-position="top"
        class="demo-form-inline"
      >
        <el-form-item label="姓名" prop="name" label-width="100px">
          <el-input
            v-model="formInline.name"
            placeholder="请输入姓名"
          ></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex" label-width="100px">
          <el-input
            v-model="formInline.sex"
            placeholder="请输入性别"
          ></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age" label-width="100px">
          <el-input
            v-model="formInline.age"
            placeholder="请输入性别"
          ></el-input>
        </el-form-item>
        <el-form-item label="电话号码" prop="phone" label-width="100px">
          <el-input
            v-model="formInline.phone"
            placeholder="请输入电话号码"
          ></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-cascader
            size="large"
            :placeholder="placeholder"
            :options="options"
            v-model="selectedOptions"
            @change="handleChange"
          >
          </el-cascader>
          <el-input
            v-model="adr"
            class="adr-input"
            placeholder="请输入详细地址"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="addData">{{
          this.alter ? "确认修改" : "添加"
        }}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Table from "./components/Table";
import { regionData, CodeToText } from "element-china-area-data";
export default {
  name: "App",
  components: {
    Table
  },
  created() {
    this.tableData = JSON.parse(localStorage.getItem("key")) || [];
  },
  computed: mapState(["user"]),
  data() {
    var validateAge = (rule, value, callback) => {
      if (value < 0 || value > 120) {
        callback(new Error("年龄不能小于0岁，不能大于120岁"));
      } else {
        callback();
      }
    };
    var validateSex = (rule, value, callback) => {
      if (value == "男" || value == "女") {
        callback();
      } else {
        callback(new Error("性别只能为 男 或 女 "));
      }
    };
    return {
      rules: {
        name: [
          { required: true, message: "姓名不能为空", trigger: "blur" },
          { min: 2, max: 10, message: "长度在 2 到 10 个字符", trigger: "blur" }
        ],
        sex: [
          { required: true, message: "性别不能为空", trigger: "blur" },
          {
            validator: validateSex,
            trigger: "blur"
          }
        ],
        age: [
          {
            required: true,
            message: "年龄不能为空",
            trigger: "blur"
          },
          {
            validator: validateAge,
            trigger: "blur"
          }
        ],
        phone: [
          {
            required: true,
            message: "号码不能为空",
            trigger: "blur"
          },
          {
            pattern: /^1[3|5|7|8|9]\d{9}$/,
            message: "请输入正确的号码格式",
            trigger: "blur"
          }
        ],
        type: [
          {
            type: "array",
            required: true,
            message: "请至少选择一个活动性质",
            trigger: "change"
          }
        ],
        resource: [
          { required: true, message: "请选择活动资源", trigger: "change" }
        ],
        desc: [{ required: true, message: "请填写活动形式", trigger: "blur" }]
      },
      index: "",
      alter: false,
      tableData: [],
      placeholder: "请选择地址",
      searchKey: "",
      dialogFormVisible: false,
      options: [
        {
          value: "440000",
          label: "广东省",
          children: [
            {
              value: "440100",
              label: "广州市",
              children: [
                { value: "440103", label: "荔湾区" },
                { value: "440104", label: "越秀区" },
                { value: "440105", label: "海珠区" },
                { value: "440113", label: "番禺区" }
              ]
            }
          ]
        }
      ],
      adr: "",
      selectedOptions: [],
      addtions: "",
      formInline: {
        name: "",
        sex: "",
        age: "",
        phone: ""
      }
    };
  },
  methods: {
    handleChange(value) {
      var name = "";
      this.selectedOptions.map(item => (name += CodeToText[item]));
      this.addtions = name;
      this.placeholder = name;
      console.log(this.addtions);
    },
    addData() {
      this.$refs.submitForm.validate(vaild => {
        if (!vaild) {
          return this.$message({
            message: "信息不正确",
            type: "warning"
          });
        }
        if (this.alter) {
          // this.tableData[this.index] = {
          //   ...this.formInline,
          //   region: this.placeholder + this.adr
          // };
          this.$set(this.tableData, this.index, {
            ...this.formInline,
            region: this.placeholder + this.adr
          });
          localStorage.setItem("key", JSON.stringify(this.tableData));
          this.$store.commit("user/setUserData", this.tableData);
          this.dialogFormVisible = false;
          this.placeholder = "请选择地址";
          this.alter = false;
        } else {
          this.formInline = {
            ...this.formInline,
            region: this.addtions + this.adr
          };
          this.tableData.push(this.formInline);
          this.formInline = {};
          this.adr = "";
          this.selectedOptions = [];
          localStorage.setItem("key", JSON.stringify(this.tableData));
          this.$store.commit("user/setUserData", this.tableData);
          this.dialogFormVisible = false;
          this.placeholder = "请选择地址";
        }
      });
    },
    openDia(i, r) {
      console.log(i);
      this.alter = true;
      this.index = i;
      this.dialogFormVisible = true;
      this.formInline = { ...r };
      this.placeholder = r.region.slice(0, 9);
      this.adr = r.region.slice(9);
    },
    search() {
      if (!this.searchKey) return;
      let searchData = JSON.parse(localStorage.getItem("key"));
      this.tableData = searchData.filter(i => {
        return i.name.indexOf(this.searchKey) !== -1;
      });
      console.log(this.tableData);
    },
    returnData() {
      this.tableData = JSON.parse(localStorage.getItem("key"));
    },
    cancel() {
      this.alter = false;
      this.dialogFormVisible = false;
      this.formInline = {};
      this.adr = "";
      this.placeholder = "请选择地址";
      this.selectedOptions = [];
    },
    repeal() {
      console.log(this.user.userData, this.tableData);
      if (this.user.userData.length > this.tableData.length) {
        this.$confirm("确定撤销", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.tableData = this.user.userData;
            localStorage.setItem("key", JSON.stringify(this.tableData));
            this.$message({
              type: "success",
              message: "撤销成功!"
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消撤销"
            });
          });
      } else {
        this.$message({ type: "info", message: "无法撤销" });
      }
    }
  }
};
</script>

<style scoped  lang="less">
#app {
  margin: 0 auto;
  margin-top: 60px;
  border: 1px solid;
  width: 1200px;
  height: 600px;
}
.header {
  height: 80px;
  line-height: 80px;
  text-align: center;
  background-color: #eee;
}
.handle {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}
.search-input {
  width: 175px;
}
/deep/ .search-input .el-input__inner {
  height: 28px;
  line-height: 28px;
  &::placeholder {
    font-size: 12px;
  }
}

/deep/ .el-form--inline .el-form-item {
  margin-right: 45px;
}
.adr-input {
  width: 235px;
}
</style>



