<template>
  <div>
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="30"></el-table-column>
      <el-table-column prop="date" type="index" label="序号" align="center">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column label="姓名" width="120">
        <template slot-scope="scope">{{ scope.row.name }}</template>
      </el-table-column>
      <el-table-column prop="age" label="年龄" width="120"> </el-table-column>
      <el-table-column prop="sex" label="性别"> </el-table-column>
      <el-table-column prop="phone" label="联系电话"> </el-table-column>
      <el-table-column prop="region" label="地址" show-overflow-tooltip>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 20px; margin-left: 10px">
      <el-button @click="handleDeleteMore">批量删除</el-button>
    </div>
  </div>
</template>

<script>
export default {
  props: ["tableData"],
  data() {
    return {
      temp: [],
      numList: [],
      multipleSelection: []
    };
  },

  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleEdit(index, row) {
      this.$emit("openDia", index, row);
    },
    handleDelete(index) {
      if (!index) return;
      this.$alert(`确定删除该用户`, "", {
        confirmButtonText: "确定",
        callback: action => {
          if (action == "cancel") {
            this.$message({ type: "info", message: `正在取消删除` });
          } else {
            this.tableData.splice(index, 1);
            localStorage.setItem("key", JSON.stringify(this.tableData));
            this.$message({ type: "success", message: `删除成功` });
          }
        }
      });
    },
    handleDeleteMore() {
      let temp = this.multipleSelection.map(item => {
        return this.tableData.indexOf(item);
      });
      for (let i = 0; i < this.multipleSelection.length; i++) {
        this.tableData.splice(i, 1);

        localStorage.setItem("key", JSON.stringify(this.tableData));
      }
      this.toggleSelection();
    }
  }
};
</script>



