import Vuex from "vuex";
import Vue from "vue";
import user from "./user";
Vue.use(Vuex);

const store = new Vuex.Store({
    modules: {
        user,
    },
    strict: true, // 开启严格模式后，只允许通过mutation改变状态
});

window.store = store;

export default store;



